.cart-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2rem;
    min-height: 100vh;
}

.cart-wrapper {
    display: flex;
    gap: 2rem;
    max-width: 1300px;
    width: 100%;
}

.cart {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}


.cart-items {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.cart-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 1000px;
    gap: 1rem;
    background-color: #ffffff;
    padding: 1rem;
    margin-top: 1px;
    border-radius: 4px;
    border: 2px solid var(--teal-color);
}

.cart-item img {
    object-fit: cover;
    width: 130px;
    height: auto;
}
.cart-items button {
    width: 25px;
    height: 25px;
    font-size: 18px;
    border: none;
    border-radius: 50px;
    cursor: pointer;
    background-color: var(--teal-color);
    color: var(--smoke-color);
    transition: all 0.3s;
}
.cart-items button:nth-last-child(3){
    padding-bottom: 3px;
}
.cart-items button:hover {
    background-color: var(--royal-color);
}

.cart-summary {
    flex-basis: 300px;
    display: flex;
    flex-direction: column;
    margin-top: 70px;
    gap: 1.5rem;
}

.cart-total {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.promo-code {
    display: inline;
}
.promo-code p {
    text-align: center;
}

.promo-code input {
    height: 40px;
    width: 100%;
    padding: 10px;
    margin: 10px 0px;
    border: 1px solid var(--teal-color);
    background-color: white;
}
.promo-code input::placeholder {
    color: lightgray;
}

.promo-code input:focus {
    outline: none; 
    border-radius: 0; 
}
.promo-code .fa-check,
.promo-code .fa-xmark {
    height: 40px;
    padding: 10px 15px;
    border: 1px solid var(--teal-color);
    background-color: var(--teal-color);
    color: whitesmoke;
    border-left: none;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    display: flex;
    font-size: 22px;
}

.checkout-button {
    padding: 10px 20px;
    display: block;
    text-align: center;
    width: 100%;
    background-color: var(--royal-color);
    color: white;
    border: none;
    border-radius: 0px;
    cursor: pointer;
    margin: 0 auto;
    transition: all 0.3s;
    font-family: 'Playpen Sans', cursive;

}

.checkout-button:hover {
    background-color: var(--teal-color);
    box-shadow: 0px 1px 10px #3333338c
}


.cart-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
}

.cart-header h2 {
    margin: 0;
}

.cart-header button {
    padding: 0.6rem 0.9rem;
    background-color: #f44336be;
    color: white;
    border: none;
    border-radius: 34px;
    cursor: pointer;
}

.cart-header button:hover {
    background-color: #da0b0b;
}

.empty-cart {
    font-size: 1.2rem;
    font-weight: bold;
    text-align: center;
}

.cart-summary span {
    border-top: 2px solid var(--royal-color);
}

.checkout-section p {
    font-size: 12px;
    margin-top: 20px;
    color: #333;
    text-align: center;
}
.checkout-section i {
    transform: scaleX(-1);
}


@media only screen and (max-width: 1350px) {
    .cart-wrapper {
        display: grid;
        width: auto;
    }
    .promo-code input {
        text-align: center;
    }
}

@media only screen and (max-width: 1050px) {
    .cart-header h2 {
        font-size: 18px;
    }
    .cart-header button {
        font-size: 12px;
        padding: 6px 8px;
    }
    .cart-item {
        display: grid;
        grid-template-columns: 1fr;
        align-items: center;
        justify-content: space-between;
        width: auto;
        gap: 0;
        background-color: #ffffff;
        padding: 1rem;
        width: 330px;
        height: auto;

    }
    .cart-item img {
        object-fit: cover;
        width: 150px;
        height: auto;
        margin: 20px auto;
    }
    
    .cart-item button {
        justify-content: space-between;
    }
    .cart-items {
        text-align: center;
        margin: 0 auto;
    }
    .cart-items p {
        text-align: center;
        margin: 0 auto;
        margin-top: 10px;
    }
    .cart-item p:nth-last-child(2) {
        position: relative;
        top: 100%;
        margin: 0 auto;
    }
    .cart-item button:nth-last-child(1) {
        position: relative;
        left: 90%;
    }
    .cart-item button:nth-last-child(3) {
        position: relative;
        top: 197%;
    }
}