.footer {
    position: relative;
    bottom: 0;
    width: 100%;
    background: var(--teal-color);
    color: var(--smoke-color);
    overflow: hidden;
    padding: 10px 25px;
}


.footer-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 100%;
    margin: 0 auto;
}

.footer-container a {
    display: block;
    margin: 10px 0;
    text-decoration: none;
    color: var(--gray-color);
    transition: color 0.5s ease-in-out, transform 0.5s ease-in-out;
}

.footer-container a:hover {
    transform: translateX(15px);
    color: var(--royal-color);
}

.footer-container h3 {
    font-size: 18px;
}

.footer-company a {
    cursor: auto;
    pointer-events: none;
}

.app-buttons {
    display: flex;
    flex-direction: row;
}

.app-button {
    width: 150px;
    height: auto;
    object-fit: contain;
    margin-right: 20px;
    margin-top: 20px;
    cursor: pointer;
    transition: transform 0.5s ease-in-out;
}

.app-button:hover {
    transform: scale(1.2);
    filter: drop-shadow(0 0 5px var(--royal-color))
}

.footer-bottom {
    border-top: 1px solid var(--royal-color);
    padding: 10px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.copyright {
    color: rgba(245, 245, 245, 0.795);
    font-size: 12px;
}

.social-buttons {
    display: flex;
}

.social-button i {
    text-decoration: none;
    color: var(--smoke-color);
    margin-left: 35px;
    font-size: 25px;
    transition: color 0.8s ease-in-out, transform 0.8s ease-in-out
}

.social-button i:hover {
    transform: rotate(360deg);
    color: var(--royal-color);
}

@media only screen and (max-width: 1250px) {
    .footer-container {
        display: grid;
        grid-template-columns: 1fr 0fr;
        align-items: center;
    }
}

@media only screen and (max-width: 700px) {
    .footer-container {
        grid-template-columns: 1fr;
    }

    .footer-container a {
        font-size: 13px;
    }

    .footer-container h3 {
        font-size: 16px;
    }

    .app-button {
        width: 130px;
        margin-top: 10px;
        margin-bottom: 10px;
    }

    .footer-bottom {
        display: grid;
        text-align: center;
        justify-content: center;
        margin: 0 auto;
        grid-template-areas:
            "social-buttons"
            "copyright";
        align-items: start;
    }

    .social-buttons {
        grid-area: social-buttons
    }

    .copyright {
        grid-area: copyright;
    }

    .social-buttons a i {
        margin-right: 15px;
        margin-bottom: 15px;
        margin-top: 10px;
    }

}