.about-head {
    margin: 50px auto;
    text-align: center;
}

.about-head h1 {
    font-size: 25px;
}

.about-head p {
    font-size: 15px;
}

.about-section {
    padding: 20px;
}

.about-company-left {
    display: grid;
    grid-template-columns: 1fr 1fr;
    max-width: 1400px;
    gap: 60px;
    align-items: center;
    margin: 0 auto;
}

.about-image {
    display: inline-block;
    overflow: hidden;
}

.about-image img {
    width: 100%;
    border: 1px solid var(--teal-color);
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;

}

.about-image img:hover {
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -ms-transform: scale(1.1);
    -o-transform: scale(1.1);
    transform: scale(1.1);
}

.about-info {
    text-align: center;
}

.about-info h2 {
    margin-bottom: 15px;
    color: var(--royal-color);
    text-shadow: 0px 1px 2px rgba(51, 51, 51, 0.523);
}

.about-company-right {
    display: grid;
    grid-template-columns: 1fr 1fr;
    max-width: 1400px;
    gap: 60px;
    align-items: center;
    margin: 0 auto;
}

.about-values {
    max-width: 1400px;
    margin: 80px auto;
    display: grid;
    gap: 60px;
    grid-template-columns: 1fr 1fr 1fr;

}

.value-sections h2 {
    margin-bottom: 15px;
}

.value-sections i {
    color: var(--royal-color);

}


@media only screen and (max-width: 950px) {
    .about-company-left {
        display: flex;
        flex-direction: column-reverse;
        gap: 20px;
    }

    .about-image {
        display: flex;
        overflow: hidden;
        transition: none;
    }
    .about-image:hover {
        transform: none;
    }

    .about-company-right {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }
    .about-values {
        display: grid;
        grid-template-columns: 1fr;
    }
}

@media only screen and (max-width: 500px) {
    .value-sections {
        text-align: center;
    }
}