.home {
    display: grid;
    gap: 20px;
    align-items: center;
    padding: 20px;
}
.home-welcome {
    display: flex;
    text-align: center;
    justify-content: center;
    padding-top: 10px;
    margin: 0;
    margin-left: 20px;
}
.home-welcome h1 {
    font-size: 34px;
    text-shadow: 0px 1px 3px rgba(0, 0, 0, 0.500);
}
.home-welcome p {
    color: var(--royal-color);
    font-size: 22px;
    text-shadow: 0px 1px 2px var(--royal-color);
}
@media only screen and (max-width: 1250px) {
    .home-welcome h1 {
        font-size: 28px;
        text-shadow: 0px 1px 3px rgba(0, 0, 0, 0.500);
    }
    .home-welcome p {
        color: var(--royal-color);
        font-size: 18px;
        text-shadow: 0px 1px 2px var(--royal-color);
    }
}

.home-icons-left,
.home-icons-right {
    display: flex;
    flex-direction: column;
    margin-left: 120px;
    margin-right: 120px;
    margin-top: 60px;
}

.home-icons-right i,
.home-icons-left i {
    display: block;
    margin: 40px;
}

.home-icons-right i:nth-child(1) {
    transform: rotate(45deg) translateX(40px);
}

.home-icons-right i:nth-child(2) {
    transform: rotate(20deg) translateX(60px);
}

.home-icons-right i:nth-child(3) {
    transform: rotate(70deg) translateX(30px);
}

.home-icons-right i:nth-child(4) {
    transform: rotate(10deg) translateX(50px);
}

.home-icons-right i:nth-child(5) {
    transform: rotate(60deg) translateX(45px);
}

.home-icons-left i:nth-child(1) {
    transform: rotate(-45deg) translateX(-40px);
}

.home-icons-left i:nth-child(2) {
    transform: rotate(-20deg) translateX(-60px);
}

.home-icons-left i:nth-child(3) {
    transform: rotate(-70deg) translateX(-30px);
}

.home-icons-left i:nth-child(4) {
    transform: rotate(-10deg) translateX(-50px);
}

.home-icons-left i:nth-child(5) {
    transform: rotate(-60deg) translateX(-45px);
}

.fa-fire-flame-curved {
    font-size: 50px;
    color: var(--teal-color);
    filter: drop-shadow(0 1px 2px var(--royal-color))
}

.home-section {
    flex-grow: 1;
    text-align: center;
}

.image-container {
    position: relative;
    display: inline-block;
}

.image-container img {
    width: 70%;
    margin: 0 auto;
    margin-top: -5px;
    height: auto;
    border: 1px solid var(--teal-color);
    box-shadow: 0px 1px 5px var(--teal-color);

}

.hover-button {
    position: absolute;
    top: 50%;
    left: 30%;
    transform: translate(-50%, -50%);
    padding: 25px 35px;
    background-color: var(--royal-color);
    color: var(--smoke-color);
    border: 1px solid var(--teal-color);
    border-radius: 5px;
    cursor: pointer;
    font-size: 25px;
    text-shadow: 0px 2px 3px var(--teal-color);
    transition: color 0.3s ease-in-out, transform 0.1s ease-in-out, background 0.3s ease-in-out;
    overflow: hidden;
    outline: 0px solid black;
    font-family: 'Playpen Sans', cursive;

}

.hover-button.hovered {
    background-color: var(--teal-color);
    color: var(--royal-color);
    outline: 1px solid var(--royal-color);
}

.hover-button span {
    display: inline-block;
    transition: transform 0.5s ease-in-out;
}


.home-icons-left {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
}

.home-icons-right {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
}

@media only screen and (max-width: 1700px) {
    .fa-fire-flame-curved {
        font-size: 40px;
    }

    .hover-button {
        top: 50%;
        left: 30%;
        transform: translate(-50%, -50%);
        padding: 20px 30px;
        font-size: 22px;
    }

}

@media only screen and (max-width: 1600px) {
    .fa-fire-flame-curved {
        font-size: 30px;
    }

    .home-icons-left {
        top: 40%;
        transform: translateY(-40%);
    }

    .home-icons-right {
        top: 40%;
        transform: translateY(-40%);
    }
}

@media only screen and (max-width: 1450px) {

    .home-icons-left,
    .home-icons-right {
        display: flex;
        flex-direction: column;
        margin-left: 90px;
        margin-right: 90px;
    }

    .fa-fire-flame-curved {
        font-size: 25px;
    }

    .home-icons-left {
        top: 35%;
        transform: translateY(-35%);
    }

    .home-icons-right {
        top: 35%;
        transform: translateY(-35%);
    }
}

@media only screen and (max-width: 1350px) {

    .home-icons-left,
    .home-icons-right {
        display: none;
    }

    .hover-button {
        top: 50%;
        left: 20%;
        transform: translate(-50%, -50%);
        padding: 15px 20px;
        font-size: 20px;
    }

    .image-container img {
        width: 95%;
    }
}

@media only screen and (max-width: 950px) {
    .hover-button {
        top: 50%;
        left: 25%;
        transform: translate(-50%, -50%);
        padding: 15px 20px;
        font-size: 15px;
    }
}

@media only screen and (max-width: 650px) {
    .hover-button {
        top: 50%;
        left: 25%;
        transform: translate(-50%, -50%);
        padding: 10px 15px;
        font-size: 12px;
    }

    .image-container img {
        width: 100%;
    }

    .home {
        padding-top: 20px;
        gap: 20px;
    }
}

@media only screen and (max-width: 450px) {
    .hover-button {
        top: 50%;
        left: 25%;
        transform: translate(-50%, -50%);
        padding: 4px 8px;
        font-size: 12px;
        width: 100px;
    }
}




.end-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 16px;
    max-width: 1305px;
    margin: 0 auto;
    margin-top: -5px;
}

.end-img {
    width: 100%;
    overflow: hidden;
    position: relative;
    box-shadow: 0px 1px 10px var(--teal-color);
}

.end-img::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, var(--teal-color) 100%);
    mask: linear-gradient(to bottom, rgba(255, 255, 255, 1) 0%, rgb(255, 255, 255) 100%);
}

.end-img img {
    width: 100%;
    height: auto;
    display: block;
}

.material-info {
    position: absolute;
    top: 45%;
    left: 0;
    color: var(--smoke-color);
    padding: 0px 50px;
    font-size: 20px;
}

.material-info strong {
    color: var(--royal-color);
    font-size: 20px;
}


@media only screen and (max-width: 1350px) {
    .end-grid {
        width: 95%;
    }
}

@media only screen and (max-width: 1200px) {
    .material-info {
        top: 35%;
        padding: 0px 50px;
        font-size: 16px;
    }

    .material-info strong {
        font-size: 18px;
    }
}

@media only screen and (max-width: 900px) {
    .material-info {
        top: 20%;
        padding: 0px 20px;
        font-size: 14px;
    }

    .material-info strong {
        font-size: 16px;
    }
}

@media only screen and (max-width: 650px) {
    .end-grid {
        grid-template-columns: 1fr;
        padding: 0;
        margin: 0 auto;
        width: 100%;
    }

    .material-info {
        top: 30%;
        padding: 0px 20px;
        font-size: 18px;
    }

    .material-info strong {
        font-size: 20px;
    }
}

@media only screen and (max-width: 450px) {
    .material-info {
        top: 20%;
        padding: 0px 20px;
        font-size: 14px;
    }

    .material-info strong {
        font-size: 16px;
    }
}


.subscribe-news {
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    border-top: 2px solid var(--teal-color);
    border-bottom: 2px solid var(--teal-color);
    padding: 20px 0px;
}

.news-text p {
    margin-right: 25px;
    margin-top: 10px;
    font-size: 25px;
    font-weight: 600;
}

.news-text p strong {
    color: var(--royal-color);
}

.news-input {
    margin-top: 0px;
}

.news-input input {
    padding: 15px 30px;
    margin-right: 10px;
    font-size: 18px;
    border-radius: 5px;
}

.news-input input::placeholder {
    color: #757575;
}

.news-input button {
    padding: 15px 25px;
    font-size: 18px;
    align-items: center;
    text-align: center;
    justify-content: center;
    cursor: pointer;
    background-color: var(--royal-color);
    border-radius: 5px;
    transition: transform 0.5s ease-in-out, background 0.5s ease-in-out;
}

.news-input button:hover {
    transform: translateX(10px) rotate(10deg) translateY(5px);
    background-color: var(--teal-color);
}

.news-input button:hover > .fa-paper-plane {
    color: var(--royal-color);
}

.news-input button i {
    color: var(--smoke-color);
    transition: color 0.5s ease-in-out;
}

@media only screen and (max-width: 900px) {
    .subscribe-news {
        display: grid;
    }

    .news-text p {
        margin-top: 0px;
    }

    .news-input {
        display: flex;
        margin: 0 auto;
        margin-top: 10px;
        padding-right: 20px;
    }
}

@media only screen and (max-width: 600px) {
    .news-text p {
        font-size: 20px;
    }

    .news-input {
        margin-left: 0px;
    }
}

@media only screen and (max-width: 450px) {
    .news-text p {
        font-size: 16px;
        margin-left: 10px;
    }

    .news-input input {
        padding: 10px 10px;
        font-size: 16px;
        margin-left: 10px;
    }

    .news-input button {
        font-size: 16px;
        padding: 15px 20px;
    }
}

@media only screen and (max-width: 375px) {
    .news-text p {
        font-size: 14px;
    }

    .news-input input {
        padding: 10px 10px;
        font-size: 14px;
    }

    .news-input button {
        font-size: 14px;
        padding: 15px 20px;
    }
}