.shopping-page {
    background: rgb(255, 255, 255);
}

.shopping-head {
    display: flex;
    margin: 0 auto;
    width: 100%;
    height: 650px;
    position: relative;
}

.shopping-head img {
    width: 100%;
    object-fit: cover;
}

.shopping-head h1 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: var(--royal-color);
    text-shadow: 0px 1px 5px black;
}

.shopping-head h2 {
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -40%);
    color: var(--royal-color);
    text-shadow: 0px 1px 5px black;
}

@media only screen and (max-width: 1300px) {
    .shopping-head {
        height: 550px;
    }

    .shopping-head h1 {
        font-size: 25px;
    }

    .shopping-head h2 {
        font-size: 20px;
    }
}

@media only screen and (max-width: 1050px) {
    .shopping-head {
        height: 500px;
    }

    .shopping-head h1 {
        font-size: 23px;
    }

    .shopping-head h2 {
        font-size: 18px;
    }
}

@media only screen and (max-width: 950px) {
    .shopping-head {
        height: 450px;
    }

    .shopping-head h1 {
        font-size: 20px;
    }

    .shopping-head h2 {
        font-size: 15px;
    }
}

@media only screen and (max-width: 850px) {
    .shopping-head h1 {
        text-align: center;
    }

    .shopping-head h2 {
        margin-top: 10px;
        text-align: center;
    }
}

@media only screen and (max-width: 450px) {
    .shopping-head h1 {
        font-size: 16px;
    }

    .shopping-head h2 {
        font-size: 11px;
        padding-top: 10px;
    }

    .shopping-head {
        height: 250px;
    }
}

.shop-filter {

    margin: 0 auto;
    align-items: center;
    justify-content: center;
    width: 100%;
    display: flex;
    padding-top: 40px;
    padding-bottom: 50px;
}

.shop-filter button {
    margin: 0 20px;
    padding: 10px 25px;
    cursor: pointer;
    font-size: 15px;
    border: none;
    border-radius: 3px;
    border: 2px solid black;
    transition: all 0.3s;
    font-size: 16px;
    font-family: 'Playpen Sans', cursive;
}

.shop-filter button:hover {
    box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.472);
    transform: translateY(-5px)
}

@media only screen and (max-width: 860px) {
    .shop-filter button {
        padding: 5px 15px;
        font-size: 14px;
    }
}

@media only screen and (max-width: 450px) {
    .shop-filter button {
        padding: 5px 15px;
        font-size: 12px;
        margin: 0 auto;
    }
}



.active1 {
    background-color: var(--royal-color);
    color: var(--smoke-color);
    transform: translateY(-5px)
}

.special-section,
.all-hoodies-section,
.limited-hoodies-section {
    display: grid;
    margin: 0 100px;
    padding-bottom: 100px;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 60px;
    justify-items: center;
}

@media (min-width: 768px) {

    .special-section,
    .all-hoodies-section,
    .limited-hoodies-section {
        grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    }
}


.all-hoodies-section,
.limited-hoodies-section {
    padding-top: 35px;
}

.item-img {
    overflow: hidden;
}

.hoodie-item {
    width: 300px;
    height: 450px;
    margin-bottom: 0px;
    overflow: hidden;
}


.item-img p {
    width: 50px;
    height: 25px;
    margin: 5px;
    font-size: 14px;
    justify-content: center;
    text-align: center;
    background-color: gray;
    color: var(--smoke-color);
}

.hoodie-item img {
    width: 100%;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
    transform-origin: top top;
    cursor: pointer;
}

.hoodie-item img:hover {
    -webkit-transform: scale(1.8);
    -moz-transform: scale(1.8);
    -ms-transform: scale(1.8);
    -o-transform: scale(1.8);
    transform: scale(1.8);
    transform-origin: top top;
}

.item-title {
    margin-left: 5px;
}

.item-title h2 {
    font-size: 18px;
}

.item-title p {
    font-size: 16px;
}

@media only screen and (max-width: 450px) {
    .hoodie-item {
        text-align: center;
        margin: 0 auto;
        justify-content: center;
        align-items: center;
        width: 100%;
    }

    .special-section,
    .all-hoodies-section,
    .limited-hoodies-section {
        text-align: center;
        margin: 0 auto;
        justify-content: center;
        align-items: center;
        width: 60%;
    }
}


.black-color,
.blue-color,
.gray-color,
.green-color,
.purple-color,
.lightblue-color,
.lightgray-color {
    display: inline-block;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    margin-right: 5px;
    margin-top: 5px;
}

.black-color {
    background-color: black;
}

.blue-color {
    background-color: darkblue;
}

.gray-color {
    background-color: gray;
}

.green-color {
    background-color: green;
}

.purple-color {
    background-color: purple;
}

.lightblue-color {
    background-color: rgb(0, 68, 255);
}

.lightgray-color {
    background-color: lightgray;
}


.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.363);
    z-index: 998;
}

.dialog {
    width: 800px;
    height: 800px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 20px;
    background-color: rgb(255, 255, 255);
    z-index: 999;
}

.dialog .fa-times {
    display: flex;
    justify-content: end;
    font-size: 25px;
    cursor: pointer;
    color: var(--teal-color);
    transition: all 0.3s ease-in-out;
}

.dialog .fa-times:hover {
    color: var(--royal-color);
}

.dialog-info {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
}

.dialog-img img {
    width: 100%;
    margin: 0 auto;
    margin-bottom: 0px;
    padding-bottom: 0px;
}

.dialog-color-img {
    margin: 20px 0px;
}

.dialog-color-img img {
    width: 80px;
    margin-right: 10px;
    cursor: pointer;
}

.dialog-content {
    position: relative;
}

.dialog-content b {
    margin-left: 40px;
}

.dialog-stars {
    display: flex;
}

.dialog-stars i {
    padding-top: 4px;
    padding-right: 4px;
    color: var(--royal-color)
}


.dialog-content span {
    content: '';
    background-color: var(--royal-color);
    width: 100%;
    height: 2px;
    position: absolute;
}

.dialog-size {
    margin: 50px 5px;
}

.size-buttons button {
    width: 50px;
    height: 50px;
    margin-top: 20px;
    margin-right: 20px;
    cursor: pointer;
    border: none;
    background-color: rgb(255, 255, 255);
    font-weight: 600;
}

.size-buttons button:hover {
    background-color: #b4b4b4;
}

.size-buttons button:focus {
    background-color: var(--teal-color);
    color: var(--smoke-color);
}

.dialog-description {
    margin: -50px auto;
    position: relative;
    right: 106%;
    top: 10px;
}

.dialog-buy {
    display: flex;
    position: relative;
    top: 50px;
    left: 115px;
}

.dialog-buy button {
    padding: 10px;
    border-radius: 5px;
    border: 2px solid var(--teal-color);
    background-color: var(--smoke-color);
    color: var(--smoke-color);
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s;
    background-color: var(--royal-color);
}

.dialog-buy button:hover {
    border: 2px solid var(--royal-color);
    background-color: var(--teal-color);
    color: var(--smoke-color);
    transform: translateY(-5px);
    box-shadow: 0px 1px 8px var(--royal-color)
}

.size-chart-link {
    position: relative;
    top: -100px;
    text-align: start;
    width: 20%;
    height: auto;
    background-color: transparent;
    border: none;
    cursor: pointer;
    text-decoration: underline;

}

.size-chart-link:hover {
    color: var(--royal-color);
}

.size-chart-container {
    position: fixed;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -40%);
    background-color: white;

    padding: 20px;
    border: 2px solid #ccc;

    border-radius: 5px;

    z-index: 999;

    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);

}

.size-chart-container img {
    display: block;
    margin: 0 auto;

    height: auto;

}

.size-chart-container img:nth-last-child(3) {
    width: 100%;
}





