.service-head {
    margin: 0 auto;
    margin-top: 50px;
    text-align: center;
}

.service-head h1 {
    font-size: 25px;
}

.service-head p {
    font-size: 15px;
}

.customer-service-section {
    max-width: 750px;
    margin: 100px auto;
    display: grid;
    grid-template-columns: 1fr 4fr;
}

.service-buttons {
    display: grid;
    grid-template-columns: 1fr;
    height: 500px;
}

.service-buttons button {
    font-size: 15px;
    position: relative;
    cursor: pointer;
    font-weight: 600;
    width: 200px;
    height: 50px;
    border-radius: 4px;
    transition: all 0.3s ease-in-out;
    user-select: none;
}

.service-buttons button.active {
    color: var(--smoke-color);
    background-color: var(--royal-color);
    box-shadow: 0px 1px 3px var(--teal-color);
}

.service-buttons button i {
    position: absolute;
    top: 33%;
    right: 5%;
    transition: transform 0.3s ease-in-out;
    font-size: 16px;
}

.accordion {
    display: flex;
    height: auto;
    width: 100%;
    margin-left: 50px;
    color: white;
    flex-direction: column;
    transition: all 0.3s ease-in-out;
}

.accordion h1 {
    color: var(--teal-color);
    font-size: 25px;
    margin-bottom: 15px;
    display: flex;
    text-align: start;
}

.accordion h2 {
    color: var(--teal-color);
    font-size: 20px;
    margin-bottom: 15px;
    display: flex;
    text-align: start;
}
.accordion ul {
    margin-bottom: 15px;
    margin-left: 20px;
}
.accordion ul li {
    font-size: 15px;
    color: var(--teal-color);
}
.accordion .ul-dots li::before {
    content: "\2022";
    color: var(--royal-color);
    font-weight: bold;
    display: inline-block; 
    width: 1em;
    margin-left: -1em;
}
.accordion ol {
    margin-bottom: 15px;
    margin-left: 20px;
}
.accordion ol li {
    font-size: 15px;
    list-style-type:decimal;
    color: var(--teal-color);
}
.accordion p {
    color: var(--teal-color);
    font-size: 15px;
    margin-bottom: 15px;
    display: flex;
    text-align: start;
    width: 91%;
}


.accordion .accordion-question {
    background-color: var(--teal-color);
    border: 2px solid var(--teal-color);
    margin-bottom: 15px;
    width: 500px;
    transition: all 0.3s ease-in-out;
    user-select: none;
}

.accordion .accordion-question.open {
    outline: none;
    border: 2px solid var(--royal-color);
    transition: all 0.3s ease-in-out;
    box-shadow: 0px 1px 3px var(--royal-color);
}

.accordion .accordion-question .accordion-title {
    color: var(--smoke-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    cursor: pointer;
    padding: 15px;
}

.accordion .accordion-question .content {
    color: var(--smoke-color);
    transition: all 0.5s cubic-bezier(0, 1, 0, 1);
    align-items: center;
    padding: 15px;
    font-family: Arial, Helvetica, sans-serif;
    line-height: 22px;
}

.accordion .accordion-question:hover {
    border: 2px solid var(--royal-color);
}

.form-section {
    position: relative;
}

.errorMessage {
    position: absolute;
    padding: 5px 0px;
    top: -22px;
    left: 0;
    color: red;
    font-size: 12px;
}

.form-section>input {
    width: 350px;
    height: 60px;
    padding: 15px;
    font-size: 15px;
    border: 2px solid var(--teal-color);
    outline-color: var(--royal-color);
}


.form-section>.form-control {
    margin-bottom: 15px;
}

.form-section>textarea {
    resize: none;
    width: 350px;
    height: 140px;
    padding: 15px;
    font-size: 16px;
    border: 2px solid var(--teal-color);
    display: flex;
    outline-color: var(--royal-color);
}


.fake-reCaptcha-container {
    padding-left: 20px;
    padding-bottom: 10px;
}

.submit-btn {
    width: 350px;
    height: 60px;
    font-size: 16px;
    border: 2px solid var(--teal-color);
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}
.submit-btn:hover {
    color: var(--smoke-color);
    background-color: var(--royal-color);
    box-shadow: 0px 1px 3px var(--teal-color);
}

.rotate {
    transform: rotate(180deg);
}


@media only screen and (max-width: 900px) {
    .customer-service-section {
        width: 50%;
        grid-template-columns: 1fr;
        align-items: center;
        justify-content: center;
        margin: 50px auto;
    }
    .all-sections {
        margin: 0 auto;
        margin-top: 50px;
        justify-content: center;
    }
    .accordion {
        margin: 0 auto;
        justify-content: center;
        align-items: center;
    }
    .service-buttons {
        grid-template-columns: 1fr 1fr 1fr;
        gap: 20px;
        height: auto;
    }
    .service-buttons button {
        width: 150px;
        font-size: 11px;
    }
    .service-buttons button i {
        font-size: 12px;
        top: 38%;
        left: 90%;
    }
    .rotate {
        transform: rotate(90deg);
    }
    .accordion ul li {
        margin-left: 15px;
    }
}
@media only screen and (max-width: 550px) {
    .service-buttons  {
        grid-template-columns: 1fr;
        justify-content: center;
        align-items: center;
        margin: 0 auto;
    }
    .service-buttons button {
        width: 250px;
        font-size: 14px;
    }
    .accordion h1 {
        text-align: center;
    }
    .accordion h2 {
        text-align: center;
    }
    .accordion p {
        font-size: 15px;
        text-align: center;
        width: auto;
    }
    .accordion ul {
        margin: 20px auto;
    }
    .accordion ul li{
        width: 300px;
        margin: 0 auto;
        text-align: center;
    }
    .accordion .accordion-question {
        width: 330px;
    }
    .form-section {
        margin: 0 auto;
        justify-content: center;
        display: flex;
    }
    .form-section>input {
        width: 330px;
        margin: 0 auto;
    }
    .form-section>textarea {
        width: 330px;
    }
    .submit-btn {
        width: 330px;
    }
    .fake-reCaptcha-container {
        margin: 0 auto;
        padding: 15px;
    }
}